import { Component } from 'react';

class MainUl extends Component{
    render(){        
      var 엘아이=[]
      var data = this.props.data              
      var i=0      
      while(i<data.length){      
        엘아이.push(
           <li>  
             <a href="" 
            
             data-id={data[i].id}
            onClick={function(e){                                      
            e.preventDefault(); 
            e.target.className = '';
            e.target.className = 'on';
            //  debugger
            this.props.onChangePage(e.target.dataset.id) 
            }.bind(this)}  
            >
            {data[i].menu}            
            </a>            
          </li>
             )
        i=i+1        
      }
      return(
        <div className="ul--group">          
          <ul className="mainul">
              {엘아이}        
          </ul>
        </div>
      )
    }
  }

  export default MainUl;