import { Component } from 'react';


class Footer extends Component{
    render(){
      var itUrl="http://mck156.dothome.co.kr"
      var 푸터data=this.props.푸터메뉴프롭
      var ss=this.props.푸터파일프롭
      var 푸터엘아이=[]
      var i=0
      while(i<푸터data.length){
          푸터엘아이.push(
            <li> 
             <a href=""              
             data-id={푸터data[i]}
            onClick={function(e){                                      
            e.preventDefault();                 
            this.props.onChangePage(e.target.dataset.id) 
            }.bind(this)}  
            >              
              {푸터data[i]}
            </a>            
          </li>
          )
        i=i+1
      }
      return(
        <footer>
          <ul className="fu--ul">
            {푸터엘아이}     
          </ul>
          <p>
          <span>
          **주의**
          </span>
            <span>
            이 문서는 보안정보가 담긴 문서입니다.		
            </span>
            <span>
            관계자 외에 배포하면 안되며 해당목적 이외에 사용하시면 불법행위임을 알려드립니다.		
            </span>
            <span>
            불법행위로 인한 불이익 등을 책임지지 않음을 알려드립니다.
            </span>
          </p>
        </footer>
      )
    }
  }
  export default Footer;