import { Component } from 'react';

class SubUl extends Component{
    render(){      
      var 보조엘아이=[]      
      var data = this.props.선택메뉴프롭      
      var itUrl="http://mck156.dothome.co.kr"     
      var i=0      
      while(i<data.length){      
        보조엘아이.push(
           <li> 
             <a href=""              
             data-id={data[i]}
            onClick={function(e){                                      
            e.preventDefault();    
            // debugger             
            this.props.onChangePage(e.target.dataset.id) 
            }.bind(this)}  
            >              
              {data[i]}
            </a>            
          </li>
             )
        i=i+1        
      }
      if(this.props.메뉴선택프롭===0){
        return(
          <nav>
            <div className="yogi">
              <iframe src={itUrl+"/doc/etc/gong.html"} ></iframe>
            </div>
          </nav>  
        )
      }else {
      return (
          <nav>
            <ol className={"m--mode"+this.props.메뉴선택프롭}>                 
              {보조엘아이}
            </ol>
          </nav>         
         )
        }
    }  
  }

  export default SubUl;