import { Component } from 'react';

class Naeyong extends Component{  
  render(){      
    var diro=<a href=""                     
                onClick={function(e){                                      
                e.preventDefault();     
                this.props.onChangePage() 
                }.bind(this)}  
              >뒤로</a>
    var itUrl="http://mck156.dothome.co.kr"

    if(this.props.받는모드프롭==="어플정보"){ return( 
      <div className="if--group"><article> <img src={itUrl+"/img/어플정보.png"} ></img></article>
      <div className="diro--group"> {diro}</div></div> ) }

    if(this.props.받는모드프롭==="홈"){
          return( <article className="ho">        
            </article> ) }//if아웃 
    if(this.props.받는모드프롭==="선택"){
      return( <article>           
        </article> ) }//if아웃
    
    //basic
    var 파일이름=this.props.파일이름프롭
    var 기초모드이름=this.props.비교배열프롭
    var i=0
    while(i<파일이름.length){
      if(this.props.받는모드프롭===기초모드이름[i]){ return( 
        <div className="if--group"><article> <iframe src={itUrl+"/doc/basic/"+파일이름[i]+".html"} ></iframe></article>
        <div className="diro--group"> {diro}</div></div> ) }
      i=i+1
    }
    // 비번    
    var v=0
    while(v<16){
      var v2=v+100      
      if(this.props.받는모드프롭===v2+" 비번"){ return( 
        <div className="if--group"><article> <img src={itUrl+"/img/bi/"+v2+".png"} ></img></article>
         {/* <div className="if--group"><article> <iframe src={itUrl+"/img/bi/"+v2+".png"} ></iframe></article> */}
        <div className="diro--group"> {diro}</div></div> ) }
      v=v+1
    }
    // 팁
    var w=0
    while(w<16){
      var w2=w+100
      if(this.props.받는모드프롭===w2+" 팁"){ return( 
        <div className="if--group"><article> <iframe src={itUrl+"/doc/tip/"+w2+" 팁"+".html"} ></iframe></article>
        <div className="diro--group"> {diro}</div></div> ) }
      w=w+1
    }
    // 화장실어디
    var y=0
    while(y<16){
      var y2=y+100
      if(this.props.받는모드프롭===y2+" 위치"){ return( 
        <div className="if--group"><article> <iframe src={itUrl+"/doc/tip/"+y2+" 위치"+".html"} ></iframe></article>
        <div className="diro--group"> {diro}</div></div> ) }
      y=y+1
    }
    // 푸터    
    var 푸터이름=this.props.푸터파일프롭
    var 푸터비교=this.props.푸터메뉴프롭
    var f=0
    while(f<푸터이름.length){
      if(this.props.받는모드프롭===푸터비교[f]){ return( 
        <div className="if--group"><article> <iframe src={itUrl+"/doc/etc/"+푸터이름[f]+".html"} ></iframe></article>
        <div className="diro--group"> {diro}</div></div> ) }
      f=f+1
    }
    }
  }//class 아웃


  export default Naeyong;